<template>
  <section id="works" >
    <div class="container">
      <!-- section title -->
      <h2 class="section-title wow fadeInUp">Recent works</h2>
      <div class="spacer" data-height="60"></div>
      <!-- portfolio filter (desktop) -->
      <ul class="portfolio-filter list-inline wow fadeInUp">
        <li class="current list-inline-item" data-filter="*">Everything</li>
        <li class="list-inline-item" data-filter=".laravel">Laravel</li>
        <li class="list-inline-item" data-filter=".wordpress">Wordpress</li>

        <li class="list-inline-item" data-filter=".vue">Vue</li>
      </ul>
      <!-- portfolio filter (mobile) -->
      <div class="pf-filter-wrapper">
        <select class="portfolio-filter-mobile">
          <option value="*">Everything</option>
          <option value=".laravel">Laravel</option>
          <option value=".wordpress">Wordpress</option>
          <option value=".vue">Vue</option>
        </select>
      </div>
      <!-- portolio wrapper -->
      <div class="row portfolio-wrapper" v-if="posts.length > 0">
        <div
          v-for="(post, index) in posts"
          :key="index"
          :class="`col-md-4 col-sm-6 grid-item ${post._embedded['wp:term'][1][0].name}`"
        >
          <a :href="`#${post.id}`" class="work-content">
            <div class="portfolio-item rounded shadow-dark">
              <div class="details">
                <span class="term">{{
                  post._embedded["wp:term"][1][0].name
                }}</span>
                <h4 class="title">{{ post.title.rendered }}</h4>
                <span class="more-button"><i class="icon-options"></i></span>
              </div>
              <div class="thumb">
                <img
                  v-if="post._embedded['wp:featuredmedia']"
                  :alt="post.title.rendered"
                  :src="post._embedded['wp:featuredmedia'][0].source_url"
                />
                <div class="mask"></div>
              </div>
            </div>
          </a>
          <div :id="post.id" class="white-popup zoom-anim-dialog mfp-hide">
            <img
              v-if="post._embedded['wp:featuredmedia']"
              :alt="post.title.rendered"
              :src="post._embedded['wp:featuredmedia'][0].source_url"
            />
            <h2>{{ post.title.rendered }}</h2>
            <div :v-html="post.content.rendered"></div>
            <a :href="post.link" class="btn btn-default">View more</a>
          </div>
        </div>
      </div >


      <!-- more button -->
      <div class="load-more text-center mt-4">
        <a class="btn btn-default" href="https://khyrulkabir.ga"
          ><i class="fas fa-spinner"></i> Load more</a
