<script>
import axios from "axios";
import moment from "moment";

export default {
  data() {
    return {
      // Wordpress Posts Endpoint
      postsUrl: "https://khyrulkabir.ga/wp-json/wp/v2/posts?categories=26",
      queryOptions: {
        per_page: 3, // Only retrieve the 10 most recent blog posts.
        page: 1, // Current page of the collection.
        _embed: true, //Response should include embedded resources.
      },
      // Returned Posts in an Array
      posts: [],
    };
  },
  methods: {
    // Get Recent Posts From WordPress Site
    getRecentMessages: async function () {
      await axios
        .get(this.postsUrl, {
          params: this.queryOptions,
          withCredentials: false,
        })
        .then((response) => {
          this.posts = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getPostDate(date) {
      return moment(date).format("lll");
    },
  },
  mounted() {
    this.getRecentMessages();
  },
};
</script>