<template>
  <section id="blog">
    <div class="container">
      <!-- section title -->
      <h2 class="section-title wow fadeInUp">Latest Posts</h2>
      <div class="spacer" data-height="60"></div>
      <div class="row blog-wrapper">
        <div class="col-md-4" v-for="(post, index) in posts"
             :key="index">
          <!-- blog item -->
          <div class="blog-item rounded bg-white shadow-dark wow fadeIn">
            <div class="thumb">
              <a :href="post._embedded['wp:term'][0][0].link">
                <span class="category">{{post._embedded['wp:term'][0][0].name}}</span>
              </a>
              <a href="#">
                <img
                    v-if="post._embedded['wp:featuredmedia']"
                    :src="post._embedded['wp:featuredmedia'][0].source_url"
                    :alt="post.title"
                />
              </a>
            </div>
            <div class="details">
              <h4 class="my-0 title">
                <a :href="post.link">{{post.title.rendered}}</a>
              </h4>
              <ul class="list-inline meta mb-0 mt-2">
                <li class="list-inline-item">{{ getPostDate(post.date) }}</li>
                <li class="list-inline-item">{{post._embedded['author'][0].name}}</li>
              </ul>
            </div>
          </div>
        </div>

      </div>
    </div>
  </section>
</template>


<script>
import axios from "axios";
import moment from "moment";
export default {
  data() {
    return {
      // Wordpress Posts Endpoint
      postsUrl: "https://khyrulkabir.ga/wp-json/wp/v2/posts",
      queryOptions: {
        per_page: 3, // Only retrieve the 10 most recent blog posts.
        page: 1, // Current page of the collection.
        categories_exclude: 26,
        _embed: true, //Response should include embedded resources.
      },
      // Returned Posts in an Array
      posts: [],
    };
  },
  methods: {
    // Get Recent Posts From WordPress Site
    getRecentMessages: async function () {
      await axios.get(
          this.postsUrl,
          {
            params: this.queryOptions,
            withCredentials:false,
          }
      )
          .then((response)=> {this.posts = response.data})
          .catch((error)=>{console.log(error)});
    },
    getPostDate(date) {
      return moment(date).format("lll");
    },
  },
  mounted() {
    this.getRecentMessages();
  },
};
</script>